import React from 'react';
import { NavLink } from 'react-router-dom';
import styles from './Navbar.module.css';

const Navbar = () => {
  return (
    <div className={styles.tabBar}>
      <NavLink to="/" className={({ isActive }) => isActive ? styles.activeTab : styles.tab}>
        Home
      </NavLink>
      <NavLink to="/about" className={({ isActive }) => isActive ? styles.activeTab : styles.tab}>
        About
      </NavLink>
      <NavLink to="/projects" className={({ isActive }) => isActive ? styles.activeTab : styles.tab}>
        Projects
      </NavLink>
    </div>
  );
};

export default Navbar;
